import styled from 'styled-components';
import { themeGet } from 'styled-system';

export const PricingTableWrapper = styled.div`
  .glide__slides {
    align-items: stretch;
    .glide__slide {
      height: auto;
    }
  }
  height: auto;
`;

const PricingTable = styled.div`
  display: inline-block;
  width: 264px;
  box-shadow: 0 2px 19px 0 rgba(0, 0, 0, 0.4);
  background-color: #242424;
  @media (min-width: 992px) {
    transition: 0.2s ease-in-out;
    &:hover {
      position: relative;
      top: -5px;
      transform: translate(0, -1em);
    }
  }
  @media (max-width: 992px) {
    width: 50%;
    margin-top: 20px;
  }
  @media (max-width: 667px) {
    width: 100%;
  }
  &:hover {
    background-color: #191919;
    border-color: #f2f4f7;
  }
`;

const PricingHead = styled.div`
  margin-bottom: 35px;
`;

const PricingHeaderBand = styled.div`
  height: 10px;
  text-align: right;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  background: ${props =>
    props.headerBackground ? props.headerBackground : 'red'};

  & img {
    height: 50px;
    padding-right: 5px;
  }
`;

const PricingPrice = styled.div`
  margin-bottom: 50px;
  text-align: center;
`;

const PricingButton = styled.div`
  text-align: center;
  margin-bottom: 20px;
  .reusecore__button {
    height: 54px;
    border: none;
    object-fit: contain;
    border-radius: 8px;
    background: ${props =>
      props.headerBackground ? props.headerBackground : 'red'};
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
  }
`;

const PricingList = styled.div`
  margin-bottom: 50px;
`;

const ListItem = styled.div`
  display: flex;
  margin-bottom: 23px;
  margin-left: 12px;
  margin-right: 12px;
  justify-content: left;
  &:last-child {
    margin-bottom: 0;
  }
  .price_list_icon {
    color: #18d379;
    margin-right: 10px;
  }
  .price_list_icon_off {
    color: #f25757;
    margin-right: 10px;
  }
  .price_list_icon_limited {
    margin-right: 10px;
    color: #fdc94f;
  }
`;

const SwitchWrapper = styled.div`
  text-align: center;
  margin-top: 20px;
  .reusecore__switch {
    .reusecore__field-label {
      font-size: 16px;
      font-weight: 400;
      color: #5c636c;
      cursor: pointer;
    }
    input[type='checkbox'] {
      &:checked {
        + div {
          width: 40px !important;
          background-color: ${themeGet('colors.primary')};
          > div {
            left: 17px !important;
          }
        }
      }
      + div {
        background-color: #f0f0f0;
        background-color: #f0f0f0;
        border: 0;
        width: 40px;
        height: 25px;
        > div {
          background-color: #fff;
          box-shadow: 0px 2px 3px 0.24px rgba(31, 64, 104, 0.25);
          width: 21px;
          height: 21px;
          top: 2px;
          left: 2px;
        }
      }
    }
  }
`;

const PricingSubTextWrapper = styled.div`
  text-align: left;
  display: inline-block;
  margin-top: 30px;
  padding-right: 20px;
  width: 65%;
  & span {
    letter-spacing: normal;
    font-size: 24px;
    line-height: 28px;
  }
`;

const PricingSubTextSubHeader = styled.div`
  color: #7f7f7f;
  display: inline;
  margin-top: 10px;
  font-size: 18px;
  line-height: 26px;
  & svg {
    float: left;
    margin-right: 8px;
    margin-top: 5px;
  }
  & div {
    float: left;
  }
`;

const PricingOptionWrapper = styled.div`
  float: right;
  width: 50px;
  & button {
    float: left;
  }

  & p {
    float: left;
    width: 120px;
    position: relative;
    top: -24px;
  }
`;

const PricingButtonWrapper = styled.div`
  text-align: right;
  margin-top: 30px;
  position: relative;
  display: inline-block;
  width: 35%;
  transition: 0.15s ease-in-out;

  img:hover {
    transition: 0.4s;
  }

  @media (max-width: 575px) {
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: right;
    margin-top: 40px;
  }
  &:before {
    content: '';
  }
  .reusecore__button {
    font-size: 14px;
    font-weight: 500;
    color: #90949b;
    background: transparent;
    height: auto;
    min-width: 100px;
    border: none;
    position: relative;
    top: -12px;
    transition: 0.15s ease-in-out;
    @media (max-width: 767px) {
      min-width: 0;
      padding: 0 15px 10px 15px;
      font-size: 14px;
    }
    &:hover {
      color: #ff4362;
    }
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0;
      left: 0;
      display: block;
      background: #ff4362;
      transform: scaleX(0);
      transform-origin: right center 0;
      transition: transform 0.7s cubic-bezier(0.19, 1, 0.22, 1) 0s;
    }
    &.active-item {
      color: #ff4362;
    }
    @media (max-width: 575px) {
      font-size: 14px;
      height: 44px;
      width: 120px;
      padding: 0 5px;
    }
  }
  > a {
    font-size: 14px;
    font-weight: 500;
    color: #3b93ff;
    padding: 0 40px;
    @media (max-width: 767px) {
      padding: 0 15px 10px 15px;
      font-size: 14px;
      height: 46px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
    }
  }
`;

export {
  PricingHead,
  PricingPrice,
  PricingButton,
  PricingList,
  ListItem,
  SwitchWrapper,
  PricingOptionWrapper,
  PricingButtonWrapper,
  PricingSubTextWrapper,
  PricingSubTextSubHeader,
  PricingHeaderBand,
};
export default PricingTable;
