import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Image from 'reusecore/src/elements/Image';
import Link from 'next/link';
import Box from 'reusecore/src/elements/Box';
import Text from 'reusecore/src/elements/Text';
import Heading from 'reusecore/src/elements/Heading';
import Button from 'reusecore/src/elements/Button';
import Container from '../../../components/UI/Container';

import Icon from 'react-icons-kit';

import { ASSOCIATIONS_TO_BANDS } from '../../../data/SaasThree';

import AssociationsWithBandsWrapper, {
  BandsRow,
  BandsRow2,
  BandWrapper,
} from './style';

const AssociationsWithBandsSection = ({
  sectionWrapper,
  secTitleWrapper,
  secHeading,
  secText,
  bandWrapper,
  bandImage,
}) => {
  const [state, setState] = useState({});

  return (
    <Box
      {...sectionWrapper}
      id="about_section"
      style={{ backgroundImage: 'linear-gradient(to right, #2c2c2c, #000000)' }}
    >
      <Container>
        <Box {...secTitleWrapper}>
          <Heading {...secHeading} content="Associations with big bands" />
        </Box>
      </Container>
      <BandsRow>
        {ASSOCIATIONS_TO_BANDS.map((band, index) => (
          <BandWrapper key={index}>
            <Image src={band.image} alt={band.name} />
          </BandWrapper>
        ))}
      </BandsRow>
      <BandsRow2>
        {ASSOCIATIONS_TO_BANDS.reverse().map((band, index) => (
          <BandWrapper key={index}>
            <Image src={band.image} alt={band.name} />
          </BandWrapper>
        ))}
      </BandsRow2>
    </Box>
  );
};

AssociationsWithBandsSection.propTypes = {
  sectionWrapper: PropTypes.object,
  row: PropTypes.object,
  secTitleWrapper: PropTypes.object,
  secHeading: PropTypes.object,
  secText: PropTypes.object,
  secTextSubheader: PropTypes.object,
};

AssociationsWithBandsSection.defaultProps = {
  sectionWrapper: {
    as: 'section',
    pt: ['20px', '20px', '20px', '20px', '100px'],
    pb: '20px',
  },
  secTitleWrapper: {
    mb: ['30px', '40px'],
  },
  secText: {
    as: 'span',
    display: 'block',
    textAlign: 'left  ',
    fontSize: '20px',
    letterSpacing: '0.15em',
    fontWeight: '700',
    color: '#ffffff',
    mb: '12px',
  },
  secHeading: {
    textAlign: 'center',
    fontSize: ['20px', '24px', '36px', '36px'],
    fontWeight: '700',
    color: '#ffffff',
    letterSpacing: '-0.025em',
    mb: '0',
    ml: 'auto',
    mr: 'auto',
    pt: ['10px', '20px', '50px', '50px'],
    pb: ['10px', '20px', '50px', '50px'],
    lineHeight: '1.12',
    width: '700px',
    maxWidth: '100%',
  },
  bandWrapper: {
    width: '100%',
    textAlign: 'center',
  },
};

export default AssociationsWithBandsSection;
