import styled from 'styled-components';
import BgImage from '../../../assets/image/saasThree/map.png';
import { themeGet } from 'styled-system';

const SectionWrapper = styled.section`
  padding: 180px 0;
  background-color: #000;
  /*background-image: url(${BgImage}),
    linear-gradient(-139deg, rgb(255, 75, 43) 0%, rgb(255, 65, 108) 100%);*/
  background-image: linear-gradient( -139deg, rgb(0,0,0) 0%, rgb(50,50,50) 100% );
  background-repeat: no-repeat;
  background-position: 25% center;
  
  .feature__block {
    align-items: center;
    margin-bottom: 14px;
    .icon__wrapper {
      color: ${themeGet('colors.secondary', '#10ac84')};
      margin-right: 10px;
    }
    .content__wrapper {
      h2 {
      }
    }
  }

  @media (max-width: 990px) {
    padding: 100px 0;
  }
  @media (max-width: 767px) {
    padding: 80px 0;
  }
  @media (max-width: 575px) {
    padding: 60px 0;
  }
`;

export default SectionWrapper;
