import React from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import Container from '../../../components/UI/Container';
import Box from 'reusecore/src/elements/Box';
import Heading from 'reusecore/src/elements/Heading';
import Text from 'reusecore/src/elements/Text';
import Button from 'reusecore/src/elements/Button';
import Image from 'reusecore/src/elements/Image';

import CompanyImgAmazon from '../../../assets/image/saasThree/companies/amazon.png';
import CompanyImgApple from '../../../assets/image/saasThree/companies/apple.png';
import CompanyImgDeezer from '../../../assets/image/saasThree/companies/deezer.png';
import CompanyImgPandora from '../../../assets/image/saasThree/companies/pandora.png';
import CompanyImgSoundCloud from '../../../assets/image/saasThree/companies/soundcloud.png';
import CompanyImgSpotify from '../../../assets/image/saasThree/companies/spotify.png';
import CompanyImgTidal from '../../../assets/image/saasThree/companies/tidal.png';
import CompanyImgYouTube from '../../../assets/image/saasThree/companies/youtube.png';

const Companies = [
  { image: CompanyImgSpotify, name: 'Spotify' },
  { image: CompanyImgAmazon, name: 'Amazon' },
  { image: CompanyImgApple, name: 'Apple' },
  { image: CompanyImgDeezer, name: 'Deezer' },
  { image: CompanyImgPandora, name: 'Pandora' },
  { image: CompanyImgSoundCloud, name: 'SoundCloud' },
  { image: CompanyImgTidal, name: 'Tidal' },
  { image: CompanyImgYouTube, name: 'YouTube' },
];

import {
  CompaniesRollWrapper,
  CompanyItemWrapper,
  CompaniesRoll,
} from './style';

const CompaniesRollSection = ({ btnWrapperStyle }) => {
  return (
    <CompaniesRollWrapper id="companies_section">
      {Companies.map((company, index) => (
        <CompanyItemWrapper>
          <Image src={company.image} alt={company.name} />
        </CompanyItemWrapper>
      ))}
    </CompaniesRollWrapper>
  );
};

CompaniesRollSection.propTypes = {
  btnWrapperStyle: PropTypes.object,
};

CompaniesRollSection.defaultProps = {
  // glide slider nav controls style
  btnWrapperStyle: {
    display: 'none',
  },
};

export default CompaniesRollSection;
