import React from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import Box from 'reusecore/src/elements/Box';
import Text from 'reusecore/src/elements/Text';
import Heading from 'reusecore/src/elements/Heading';
import Button from 'reusecore/src/elements/Button';
import Image from 'reusecore/src/elements/Image';
import Container from '../../../components/UI/Container';
import FeatureBlock from '../../../components/FeatureBlock';

import MarketingFeatureSectionWrapper from './style';
import FeatureImage from '../../../assets/image/saasThree/features/ffm-smartlinks.png';
import { MARKETING_SUITE_DATA } from '../../../data/SaasThree';

const MarketingFeatureSection = ({
  row,
  col,
  title,
  description,
  button,
  textArea,
  featureListArea,
  imageArea,
  featureTitle,
  btnStyle,
}) => {
  return (
    <MarketingFeatureSectionWrapper>
      <Container>
        <Box {...row}>
          <Box {...col} {...textArea}>
            <FeatureBlock
              title={
                <Heading
                  content="A digital suite built specifically for music marketing"
                  {...title}
                />
              }
              description={
                <Text
                  content="Market smarter, not harder; automate and optimize campaigns"
                  {...description}
                />
              }
            />
            <Box {...featureListArea}>
              {MARKETING_SUITE_DATA.map((feature, index) => (
                <FeatureBlock
                  key={`feature_point-${index}`}
                  icon={<i className={feature.icon} />}
                  iconPosition="left"
                  title={<Heading content={feature.title} {...featureTitle} />}
                />
              ))}
            </Box>
            <Box>
              <Link href="#">
                <a>
                  <Button {...button} title="LEARN MORE" />
                </a>
              </Link>
            </Box>
          </Box>
          <Box {...col} {...imageArea}>
            <Image src={FeatureImage} alt="Domain Image" />
          </Box>
        </Box>
      </Container>
    </MarketingFeatureSectionWrapper>
  );
};

MarketingFeatureSection.propTypes = {
  row: PropTypes.object,
  col: PropTypes.object,
  title: PropTypes.object,
  description: PropTypes.object,
  button: PropTypes.object,
  textArea: PropTypes.object,
  featureListArea: PropTypes.object,
  imageArea: PropTypes.object,
};

MarketingFeatureSection.defaultProps = {
  row: {
    flexBox: true,
    flexWrap: 'wrap',
    ml: '-15px',
    mr: '-15px',
    alignItems: 'center',
  },
  imageAreaRow: {
    flexDirection: 'row-reverse',
  },
  col: {
    pr: '15px',
    pl: '15px',
  },
  textArea: {
    width: ['100%', '100%', '100%', '40%', '40%'],
  },
  featureListArea: {
    width: ['100%', '100%', '100%', '100%', '100%'],
  },
  imageArea: {
    width: ['100%', '100%', '45%', '60%', '60%'],
    mb: ['40px', '40px', '0', '0', '0'],
  },
  // About us section title default style
  title: {
    fontSize: ['26px', '26px', '30px', '40px'],
    lineHeight: '1.5',
    fontWeight: '700',
    color: '#fff',
    letterSpacing: '-0.025em',
    mb: '10px',
  },
  // About us section description default style
  description: {
    fontSize: '17px',
    color: '#aaa',
    lineHeight: '1.75',
    mb: '20px',
  },
  button: {
    type: 'button',
    fontSize: '14px',
    fontWeight: '600',
    borderRadius: '4px',
    pl: '22px',
    pr: '22px',
    mt: '20px',
    colors: 'secondaryWithBg',
    minWidth: '150px',
  },
  // feature title default style
  featureTitle: {
    fontSize: '16px',
    fontWeight: '400',
    color: '#fff',
    lineHeight: '1.5',
    mb: '0px',
    letterSpacing: '-0.020em',
  },
  // Button default style
  btnStyle: {
    minWidth: '156px',
    fontSize: '14px',
    fontWeight: '500',
  },
};

export default MarketingFeatureSection;
