import styled, { keyframes } from 'styled-components';

export const CompaniesRollWrapper = styled.section`
  width: 100%;
  height: 100px;
  padding-top: 40px;
  padding-bottom: 10px;
  white-space: nowrap;
  text-align: center;
  background-image: linear-gradient(
    -139deg,
    rgb(0, 0, 0) 0%,
    rgb(50, 50, 50) 100%
  );
`;

export const CompanyItemWrapper = styled.span`
  width: 300px;
  margin-right: 5%;
  margin-left: 2%;
  float: none;

  img {
    max-height: 50%;
    display: inline;
    vertical-align: top;
    @media (max-width: 1400px) {
    }
  }
`;

export const CompaniesRoll = styled.section``;
