import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import Box from 'reusecore/src/elements/Box';
import Text from 'reusecore/src/elements/Text';
import Heading from 'reusecore/src/elements/Heading';
import Button from 'reusecore/src/elements/Button';
import Container from '../../../components/UI/Container';
import GlideCarousel from '../../../components/GlideCarousel';
import GlideSlide from '../../../components/GlideCarousel/glideSlide';
import Icon from 'react-icons-kit';

import SwitchAnnual from '../../../assets/image/saasThree/pricing/switch-annual.png';
import SwitchMonthly from '../../../assets/image/saasThree/pricing/switch-monthly.png';
import BadgeMostCommon from '../../../assets/image/saasThree/pricing/badge-most-common.png';

import { checkmark } from 'react-icons-kit/icomoon/checkmark';
import { warning } from 'react-icons-kit/icomoon/warning';
import { cross } from 'react-icons-kit/icomoon/cross';

import { PRICING_TABLE } from '../../../data/SaasThree';

import PricingTable, {
  PricingHead,
  PricingPrice,
  PricingButton,
  PricingList,
  ListItem,
  PricingOptionWrapper,
  PricingButtonWrapper,
  PricingTableWrapper,
  PricingSubTextWrapper,
  PricingSubTextSubHeader,
  PricingHeaderBand,
} from './pricing.style';

const PricingSection = ({
  sectionWrapper,
  secTitleWrapper,
  secHeading,
  secText,
  secTextSubheader,
  nameStyle,
  pricingBadge,
  descriptionStyle,
  priceStyle,
  priceLabelStyle,
  benefitStyle,
  buttonFillStyle,
  listContentStyle,
  priceLabelExtraStyle,
}) => {
  const [state, setState] = useState({
    data: PRICING_TABLE,
    active: true,
  });

  const getIcon = status => {
    let className = 'price_list_icon';
    let icon = checkmark;
    if (status === 'no') {
      className = 'price_list_icon_off';
      icon = cross;
    } else if (status === 'limited') {
      className = 'price_list_icon_limited';
      icon = warning;
    }
    return <Icon icon={icon} className={className} size={13} />;
  };

  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setTimeout(function() {
      setLoading(true);
    }, 500);
  });

  const data = state.data;
  const activeStatus = state.active;

  return (
    <Box
      {...sectionWrapper}
      id="pricing_section"
      style={{ backgroundImage: 'linear-gradient(to right, #2c2c2c, #000000)' }}
    >
      <Container>
        <Box {...secTitleWrapper}>
          <Heading
            {...secHeading}
            content="Find your perfect plan and skyrock your results"
          />

          <PricingSubTextWrapper>
            <Text
              {...secText}
              content="Select a plan that works the best for you"
            />
            <PricingSubTextSubHeader>
              <Icon icon={checkmark} size={13} />
              <Text content="All plans include unlimited links and clicks" />
            </PricingSubTextSubHeader>
          </PricingSubTextWrapper>
          <PricingButtonWrapper>
            <Button
              title="Monthly"
              className={activeStatus === false ? 'active-item' : ''}
              onClick={() => setState({ data: PRICING_TABLE, active: false })}
            />
            <img
              width="50px"
              style={{ cursor: 'pointer' }}
              src={activeStatus ? SwitchAnnual : SwitchMonthly}
              onClick={() =>
                setState({
                  data: PRICING_TABLE,
                  active: !activeStatus,
                })
              }
            />
            <PricingOptionWrapper>
              <Button
                title="Annually"
                className={activeStatus ? 'active-item' : ''}
                onClick={() => setState({ data: PRICING_TABLE, active: true })}
              />
              <Text {...benefitStyle} content={'2 Months Free'} />
            </PricingOptionWrapper>
          </PricingButtonWrapper>
        </Box>
        <PricingTableWrapper>
          {data.map((pricingTable, index) => (
            <PricingTable
              key={index}
              freePlan={pricingTable.freePlan}
              className="pricing_table"
            >
              <PricingHead>
                <PricingHeaderBand
                  headerBackground={pricingTable.headerBackground}
                >
                  {pricingTable.name === 'PRO' && (
                    <Box {...pricingBadge}>
                      <img src={BadgeMostCommon} />
                    </Box>
                  )}
                </PricingHeaderBand>
                <Heading content={pricingTable.name} {...nameStyle} />
              </PricingHead>
              <PricingPrice>
                <Text
                  content={
                    activeStatus ? pricingTable.priceAnnual : pricingTable.price
                  }
                  {...priceStyle}
                />
                <Text content={pricingTable.priceLabel} {...priceLabelStyle} />
                {activeStatus && (
                  <Text content="* Billed Monthly" {...priceLabelExtraStyle} />
                )}
              </PricingPrice>
              <PricingList>
                {pricingTable.listItems.map((item, index) => (
                  <ListItem key={`pricing-table-list-${index}`}>
                    {getIcon(item.available)}
                    <Text content={item.content} {...listContentStyle} />
                  </ListItem>
                ))}
              </PricingList>
              <PricingButton headerBackground={pricingTable.headerBackground}>
                <Link href={pricingTable.url}>
                  <a>
                    <Button
                      title={pricingTable.buttonLabel}
                      {...buttonFillStyle}
                    />
                  </a>
                </Link>
              </PricingButton>
            </PricingTable>
          ))}
        </PricingTableWrapper>
      </Container>
    </Box>
  );
};

PricingSection.propTypes = {
  sectionWrapper: PropTypes.object,
  row: PropTypes.object,
  secTitleWrapper: PropTypes.object,
  secHeading: PropTypes.object,
  secText: PropTypes.object,
  secTextSubheader: PropTypes.object,
  nameStyle: PropTypes.object,
  descriptionStyle: PropTypes.object,
  priceStyle: PropTypes.object,
  priceLabelStyle: PropTypes.object,
  priceLabelExtraStyle: PropTypes.object,
  listContentStyle: PropTypes.object,
};

PricingSection.defaultProps = {
  sectionWrapper: {
    as: 'section',
    pt: ['60px', '80px', '80px', '80px', '100px'],
    pb: '20px',
  },
  secTitleWrapper: {
    mb: ['10px', '10px'],
  },
  secText: {
    as: 'span',
    display: 'block',
    textAlign: 'left  ',
    fontSize: '20px',
    letterSpacing: '0.15em',
    fontWeight: '700',
    color: '#ffffff',
    mb: '12px',
  },
  secHeading: {
    textAlign: 'center',
    fontSize: ['20px', '24px', '38px', '38px'],
    fontWeight: 'bold',
    color: '#ffffff',
    letterSpacing: '-0.025em',
    mb: '0',
    ml: 'auto',
    mr: 'auto',
    pt: '60px',
    pb: '70px',
    lineHeight: '1.13',
    width: '500px',
    maxWidth: '100%',
    letterSpacing: 'normal',
  },
  col: {
    width: [1, 1 / 2, 1 / 2, 1 / 3],
    pr: '15px',
    pl: '15px',
  },
  nameStyle: {
    fontSize: ['20px', '20px', '22px', '22px', '22px'],
    fontWeight: '700',
    color: '#ffffff',
    letterSpacing: '-0.025em',
    textAlign: 'center',
    mb: '12px',
    pt: '50px',
  },
  descriptionStyle: {
    fontSize: ['15px', '16px', '16px', '16px', '16px'],
    color: '#ffffff',
    lineHeight: '1.75',
    textAlign: 'center',
    mb: '0',
  },
  priceStyle: {
    as: 'span',
    display: 'inline',
    fontSize: ['36px', '36px', '40px', '53px', '53px'],
    color: '#ffffff',
    textAlign: 'center',
    margin: '0 auto',
    mb: '5px',
    letterSpacing: '-0.025em',
    fontWeight: 'bold',
  },
  priceLabelStyle: {
    fontSize: ['13px', '14px', '14px', '14px', '14px'],
    color: '#6e7379',
    lineHeight: '1.75',
    display: 'inline',
    textAlign: 'center',
    mb: '0',
  },
  priceLabelExtraStyle: {
    fontSize: ['13px', '14px', '14px', '14px', '14px'],
    color: '#6e7379',
    lineHeight: '1.75',
    display: 'block',
    textAlign: 'center',
    mb: '0',
  },
  benefitStyle: {
    color: '#35d36e',
    fontSize: '14px',
  },
  buttonStyle: {
    type: 'button',
    fontSize: '14px',
    fontWeight: '600',
    borderRadius: '4px',
    pl: '10px',
    pr: '10px',
    bg: '#fff',
    color: '#2aa275',
    colors: 'primaryWithBg',
    width: '222px',
    maxWidth: '100%',
  },
  buttonFillStyle: {
    type: 'button',
    fontSize: '15px',
    fontWeight: '700',
    color: 'white',
    borderRadius: '4px',
    pl: '10px',
    pr: '10px',
    colors: 'primaryWithBg',
    minWidth: ['160px', '190px'],
    maxWidth: '100%',
    height: '48px',
  },
  listContentStyle: {
    fontSize: ['14px', '14px', '14px', '14px', '14px'],
    fontWeight: '500',
    fontStyle: 'normal',
    lineHeight: '1.5',
    color: '#d3d3d3',
    mb: '0',
  },
};

export default PricingSection;
