import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import Icon from 'react-icons-kit';
import Fade from 'react-reveal/Fade';
import { openModal, closeModal } from '@redq/reuse-modal';
import Box from 'reusecore/src/elements/Box';
import Text from 'reusecore/src/elements/Text';
import Heading from 'reusecore/src/elements/Heading';
import Button from 'reusecore/src/elements/Button';
import Image from 'reusecore/src/elements/Image';
import Container from '../../../components/UI/Container';
import TiltShape from '../TiltShape';
import Slider from 'react-slick/dist/react-slick.js';

import {
  BannerWrapper,
  SliderWrapper,
  SliderItemWrapper,
  VideoWrapper,
} from './banner.style';
import BannerImage from '../../../assets/image/saasThree/banner-image.png';
import HeroImage1 from '../../../assets/image/saasThree/hero-image-1.png';
import { ic_play_circle_filled } from 'react-icons-kit/md/ic_play_circle_filled';
import { play } from 'react-icons-kit/entypo/play';
import { HERO_SLIDES } from '../../../data/SaasThree';

// close button for modal
const CloseModalButton = () => (
  <Button
    className="modalCloseBtn"
    variant="fab"
    onClick={() => closeModal()}
    icon={<i className="flaticon-plus-symbol" />}
  />
);

const ModalContent = () => (
  <VideoWrapper>
    <iframe
      title="Video"
      src="https://www.youtube.com/embed/8ME-QAlW6Ww"
      frameBorder="0"
    />
  </VideoWrapper>
);

const BannerSection = ({
  row,
  contentWrapper,
  discountAmount,
  discountText,
  title,
  suffixStyle,
  description,
  imageWrapper,
  buttonWrapper,
  button,
  fillButton,
}) => {
  const settings = {
    dots: false,
    arrows: false,
    infinite: true,
    speed: 500,
    centerMode: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerPadding: '300px',
    focusOnSelect: true,
    accessibility: false,
    beforeChange: (current, next) => {
      //window.scroll({ top: 0, left: 0, behavior: 'smooth' });
      setState({ currentSlide: HERO_SLIDES[next].keyword });
    },
  };

  const [state, setState] = useState({
    currentSlide: HERO_SLIDES[0].keyword,
  });

  // modal handler
  const handleVideoModal = () => {
    openModal({
      config: {
        className: 'video-modal',
        disableDragging: true,
        default: {
          width: '100%',
          height: '100%',
          x: 0,
          y: 0,
        },
      },
      component: ModalContent,
      componentProps: {},
      closeComponent: CloseModalButton,
      closeOnClickOutside: true,
    });
  };

  return (
    <BannerWrapper id="banner_section">
      {/* <TiltShape className="banner-shape" /> */}
      <Container>
        <Box {...row}>
          <Box {...contentWrapper}>
            <Heading
              {...title}
              content="Your one stop shop for music "
              suffix={state.currentSlide}
              suffixStyle={suffixStyle}
            />
          </Box>
          <Box {...contentWrapper}>
            <Text
              {...description}
              content="Music focused suite of tools that helps your music grow without compromise"
            />
            <Box {...buttonWrapper}>
              <Link href="#">
                <a>
                  <Button {...fillButton} title="GET STARTED" />
                </a>
              </Link>
            </Box>
          </Box>
        </Box>
      </Container>
      <SliderWrapper>
        <Slider {...settings}>
          {HERO_SLIDES.map((slide, index) => (
            <SliderItemWrapper key={index}>
              <Image
                style={{ borderColor: slide.borderColor }}
                src={slide.image}
                alt={slide.keyword}
              />
            </SliderItemWrapper>
          ))}
        </Slider>
      </SliderWrapper>
    </BannerWrapper>
  );
};

BannerSection.propTypes = {
  row: PropTypes.object,
  contentWrapper: PropTypes.object,
  discountAmount: PropTypes.object,
  discountText: PropTypes.object,
  title: PropTypes.object,
  suffixStyle: PropTypes.object,
  description: PropTypes.object,
  imageWrapper: PropTypes.object,
  buttonWrapper: PropTypes.object,
  button: PropTypes.object,
  fillButton: PropTypes.object,
};

BannerSection.defaultProps = {
  row: {
    flexBox: true,
    flexWrap: 'wrap',
    alignItems: 'center',
    justifyContent: 'center',
  },
  contentWrapper: {
    width: ['100%', '100%', '90%', '90%', '90%'],
    mb: '0px',
  },
  title: {
    fontSize: ['24px', '32px', '38px', '38px', '38px'],
    fontWeight: 'bold',
    color: '#ffffff',
    mb: ['20px', '25px', '25px', '15px', '15px'],
    lineHeight: '1.2',
    textAlign: 'center',
    lineHeight: 1.13,
    letterSpacing: 'normal',
  },
  suffixStyle: {
    color: '#fe447d',
  },
  description: {
    fontSize: ['15px', '16px', '16px', '16px', '18px'],
    fontWeight: '500',
    color: '#aaa',
    lineHeight: '1.44',
    mb: '0',
    opacity: '0.5',
    textAlign: 'center',
    letterSpacing: 'normal',
  },
  discountAmount: {
    fontSize: ['13px', '14px', '14px', '14px', '14px'],
    fontWeight: '700',
    color: '#fff',
    mb: 0,
    as: 'span',
    mr: '0.4em',
  },
  discountText: {
    fontSize: ['13px', '14px', '14px', '14px', '14px'],
    fontWeight: '400',
    color: '#fff',
    mb: 0,
    as: 'span',
  },
  fillButton: {
    type: 'button',
    fontSize: ['13px', '14px'],
    fontWeight: '600',
    borderRadius: '4px',
    p: ['0px 15px', '8px 22px'],
    colors: 'secondaryWithBg',
    height: '46px',
    minWidth: ['auto', '150px'],
    height: ['40px', '46px'],
    minHeight: 'auto',
    color: 'white',
  },
  buttonWrapper: {
    flexBox: true,
    justifyContent: 'center',
    mt: '35px',
  },
  button: {
    type: 'button',
    fontSize: ['13px', '14px'],
    fontWeight: '600',
    borderRadius: '4px',
    p: ['0px 15px', '8px 22px'],
    color: '#fff',
    colors: 'secondary',
    height: ['40px', '46px'],
    minHeight: 'auto',
    color: 'white',
  },
};

export default BannerSection;
