import styled from 'styled-components';

const TestimonialSecWrapper = styled.section`
  padding: 80px 0;
  @media (max-width: 575px) {
    padding: 60px 0;
  }
  @media (max-width: 575px) {
    padding-left: 15px;
    padding-right: 15px;
  }

  .glide {
    &:hover {
      .glide__prev--area,
      .glide__next--area {
        /*opacity: 1;*/
      }
    }
  }

  .glide__slides {
    padding-top: 10px;
    padding-bottom: 30px;

    .glide__slide {
      opacity: 0.5; 
      pointer-events: none;
      transition: 0s ease-in-out;
      &.glide__slide--active {
        opacity: 1;
        pointer-events: auto;
        + .glide__slide {
          @media (min-width: 800px) {
            /*opacity: 1;*/
            pointer-events: auto;
          }
        }
      }
    }
  }

  .glide__controls {
    position: static;
    .glide__prev--area,
    .glide__next--area {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      /*opacity: 0;*/
      transition: 0.5s ease-in-out;
      @media (max-width: 990px) {
        display: none;
      }

      button {
        font-size: 28px;
      }
    }
    .glide__prev--area {
      left: 10%;
      @media (max-width: 1400px) {
        left: 5%;
      }
    }
    .glide__next--area {
      right: 10%;
      @media (max-width: 1400px) {
        right: 5%;
      }
    }
  }

  @media (max-width: 990px) {
    .glide__slide--active .testimonial_item {
    }
  }
`;

export const TestimonialItem = styled.div`
  border: 1px solid #f2f4f7;
  padding: 40px;
  border-radius: 5px;
  background-color: #fff;
  /*transition: 0.425s ease;*/

  &:hover {
  }

  @media (max-width: 1300px) {
    padding: 30px;
  }
`;

export const ImageWrapper = styled.div`
  img {
    width: 1000px !important;;
    height: auto;
    display: block;
  }
`;

export default TestimonialSecWrapper;
