import React, { Fragment } from 'react';
import Head from 'next/head';
import { ThemeProvider } from 'styled-components';
import Sticky from 'react-stickynode';
import { DrawerProvider } from 'common/src/contexts/DrawerContext';
import { saasThreeTheme } from 'common/src/theme/saasThree';
import { ResetCSS } from 'common/src/assets/css/style';
import {
  GlobalStyle,
  ContentWrapper,
} from 'common/src/containers/SaasThree/saasThree.style';

import BannerSection from 'common/src/containers/SaasThree/Banner';
import Navbar from 'common/src/containers/SaasThree/Navbar';
import CompaniesRollSection from 'common/src/containers/SaasThree/CompaniesRoll';
import ServiceSection from 'common/src/containers/SaasThree/Service';
import PricingSection from 'common/src/containers/SaasThree/Pricing';
import PartnerSection from 'common/src/containers/SaasThree/Partner';
import MarketingFeatureSection from 'common/src/containers/SaasThree/MarketingFeature';
import AudienceInsightsFeature from 'common/src/containers/SaasThree/AudienceInsightsFeature';
import TrialSection from 'common/src/containers/SaasThree/Trial';
import FeatureSection from 'common/src/containers/SaasThree/Feature';
import MarketingSuiteSection from 'common/src/containers/SaasThree/MarketingSuiteFeature';
import UpdateScreen from 'common/src/containers/SaasThree/UpdateScreen';
import ServicesSection from 'common/src/containers/SaasThree/Services';
import FeaturesSection from 'common/src/containers/SaasThree/Features';
import TestimonialSection from 'common/src/containers/SaasThree/Testimonial';
import TestimonialSection2 from 'common/src/containers/SaasThree/Testimonials2';
import AssociationsWithBands from 'common/src/containers/SaasThree/AssociationsWithBands';

import HeroSliderSection from 'common/src/containers/SaasThree/HeroSlider';

import Newsletter from 'common/src/containers/SaasThree/Newsletter';
import Footer from 'common/src/containers/SaasThree/Footer';

export default () => {
  return (
    <ThemeProvider theme={saasThreeTheme}>
      <Fragment>
        <Head>
          <title>Feature.fm | Your one stop shop for music marketing</title>
          <meta name="Description" content="Feature.fm" />
          <meta name="theme-color" content="#ec5555" />
          {/* Load google fonts */}
          <link
            href="https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,500i,700,900|Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i"
            rel="stylesheet"
          />
          <link
            href="https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700&amp;subset=latin-ext,vietnamese"
            rel="stylesheet"
          />
          <link
            rel="stylesheet"
            type="text/css"
            charSet="UTF-8"
            href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css"
          />
          <link
            rel="stylesheet"
            type="text/css"
            charSet="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css"
          />
        </Head>

        <ResetCSS />
        <GlobalStyle />

        <ContentWrapper>
          <Sticky top={0} innerZ={9999} activeClass="sticky-nav-active">
            <DrawerProvider>
              <Navbar />
            </DrawerProvider>
          </Sticky>
          <BannerSection />
          <CompaniesRollSection />
          <ServicesSection />
          <MarketingFeatureSection />
          <PartnerSection />
          <AudienceInsightsFeature />
          <TestimonialSection2 />
          <AssociationsWithBands />
          <FeaturesSection />
          <TrialSection />
          <Footer />
        </ContentWrapper>
      </Fragment>
    </ThemeProvider>
  );
};
