import styled from 'styled-components';
import { themeGet } from 'styled-system';

const AssociationsWithBandsWrapper = styled.section``;

const BandWrapper = styled.div`
  padding-right: 40px;
  display: inline-block;

  @media (max-width: 920px) {
    padding-right: 20px;
  }
  & img {
    border-radius: 8px;
    width: 220px;
    margin-bottom: 22px;
    @media (max-width: 1200px) {
      width: 180px;
    }
    @media (max-width: 920px) {
      width: 140px;
    }
    @media (max-width: 574) {
      width: 100px;
    }
  }
`;

const BandsRow = styled.div`
  overflow: hidden;
  width: 500%;
  left: -40px;
  position: relative;

  &img: hover {
    filter: grayscale(100%);
    cursor: pointer;
  }
`;

const BandsRow2 = styled(BandsRow)`
  left: -100px;
`;

export { BandWrapper, BandsRow, BandsRow2 };

export default AssociationsWithBandsWrapper;
